@use "../abstracts/variables";
@use "../abstracts/mixins";
@import "mediaicons";

.password {
  position: relative;
  min-height: 500px;
  margin-top: 72px;

  @include mixins.media('desktop') {
    min-height: 100vh;
    margin-top: 56px;
  }

  * {
    box-sizing: border-box;
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    @include mixins.boxFlex();

    @include mixins.media('desktop') {
      left: 50%;
      transform: translateX(-50%);
      @include mixins.boxFlex(space-between);
    }

    h1, h2 {
      text-transform: uppercase;
    }

    .list-icons-social li {
      margin-right: 0;
    }

    &--form {
      padding: 16px;
      background-color: variables.$color-white;

      @include mixins.media('desktop') {
        width: 50%;
        padding: 24px;
      }

      h2 {
        margin-bottom: variables.$margin-m;
      }

      h2, p {
        text-align: center;

        @include mixins.media('desktop') {
          display: none;
        }
      }

      &-media {
        text-align: center;

        ul {
          margin-top: variables.$margin-l;
        }
      }

      #Password, &__button, p {
        margin-bottom: variables.$margin-l;
      }

      #Password {
        font-size: variables.$font-size--general-up-m;

        @include mixins.media('desktop') {
          font-size: variables.$font-size--general-up-s;
        }
      }

      #PasswordLoginForm-password-error {
        color: variables.$color-error;
      }



      &__button {
        width: 100%;
        padding: 8px;
      }
    }

    &--info {
      display: none;

      @include mixins.media('desktop') {
        width: 48%;
        display: block;
      }

      h1 {
        margin-bottom: variables.$margin-l;
      }

      p {
        font-size: variables.$font-size--general-l;
        line-height: variables.$line-height--general-m;
      }
    }
  }
}

.newsletter {
  overflow-y: hidden;

  .letter-icon--bottom-right {
    top: 82%;
    bottom: unset !important;

    @include mixins.media('desktop') {
      top: 60%;
    }
  }
}

.errors ul li {
  text-align: center;
}

.form__message {
  font-size: variables.$font-size--general-m;
}

.list-icons-social__item a svg {
  width: 22px;
}
