@use "../abstracts/variables";
@use "../abstracts/mixins";

.header__container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  padding: 24px 0 20px;
  background-color: variables.$color-white;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);

  @include mixins.media('tablet-l') {
    padding: 17px 0 11px 50px;
  }
}

.header.grid {
  grid-template-columns: 1fr;
}

.header__logo {
  text-align: center;

  @include mixins.media('tablet-l') {
    text-align: start;
  }
}
