@use "../abstracts/variables";
@use "../theme/theme";
@use "../abstracts/mixins";

.newsletter {
  position: relative;
  padding: 80px 0 110px 0;
  background-color: variables.$color-principal;
  color: variables.$color-white;
}

.newsletter__container {
  position: relative;
  max-width: 526px;
  margin: 0 auto;
  text-align: center;
  z-index: 2;
}

.newsletter__title,
.newsletter__text {
  margin-bottom: 8px;
  color: variables.$color-white;
  text-align: center;
}

.newsletter__title {
  &.h2 {
    color: variables.$color-white;
    text-transform: uppercase;
  }
}

.newsletter__text {
  display: block;
  @extend %geralText;
}

.newsletter-form {
  .newsletter-form__input-wrapper {
    position: relative;
    border-bottom: 1px solid variables.$color-white;
    width: 100%;
    @include mixins.boxFlex;

    #klaviyo-form-footer {
      input[type="email"] {
        border: unset !important;
        background-color: transparent !important;
      }
      button {
        padding: 0 !important;
        width: 60% !important;
      }
      #Newsletter-success--newsletter {
        border: none;
      }
    }

    #klaviyo-form-my-account {
      border-bottom: 1px solid var(--color-base-principal);
      form {
        background-color: transparent !important;

        input {
          height: 45.6px !important;
          color: #000 !important;
          background-color: transparent !important;
        }

        .needsclick.kl-private-reset-css-Xuajs1 {
          padding: 0px !important;
        }
      }
      button {
        border: 1px solid rgba(0, 59, 195) !important;
        background-color: rgba(0, 59, 195) !important;
        color: #fff !important;
        width: 60% !important;
        transition: transform .3s;

        &:hover {
          background-color: #fff !important;
          color: rgba(0, 59, 195) !important;
        }
      }
      #Newsletter-success--newsletter {
        border: none;
      }
    }
    .input-container,
    .form__textfield-notlabel,
    input[type="email"] {
      width: 100%;
    }

    .input-container label {
      color: variables.$color-white;
      top: 50%;
      transform: translateY(-50%);
    }

    input[type="email"] {
      border: unset;
      padding: 25.5px 12px;
      background-color: transparent;

      &::placeholder {
        color: variables.$color-white;
      }

      input:autofill {
        border: 3px solid black;
        background-color: red;
        appearance: none;
          background-image: none;
        -webkit-appearance: none;
        -webkit-box-shadow: 0 0 0px 40rem #003BC3 inset;
        -webkit-text-fill-color: variables.$color-white;
      }

      &:-webkit-autofill {
        -webkit-box-shadow: 0 0 0px 40rem #003BC3 inset;
        -webkit-text-fill-color: variables.$color-white;
      }
    }

    button {
      padding-top: 10px;
      padding-bottom: 10px;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);

      span {
        margin: 0;
        font-size: variables.$font-size--general-m;
        line-height: variables.$line-height--general-up-s;
      }
    }


  }

  &__wrapper {
    --columns: 1fr;
  }

  &__message {
      font-size: variables.$font-size--general-m;
  
    &--success {
      color: variables.$color-principal;
      margin-top: 20px;
      font-size: variables.$font-size--general-m;
      background-color: variables.$color-secondary;
    }
    &--error {
      display: none;
      border-color: variables.$color-newsletter-error;
      margin-top: 20px;
      background-color: variables.$color-newsletter-error-bg;
    }
  }
}

.newsletter__letter-stamp.letter-icon.letter-icon--bottom-right {
  display: block;
  position: absolute;
  right: 0;
  bottom: -12%;
  transform: unset;
  height: auto;
  width: 90px;

  & img {
    height: auto;
    width: auto;
  }

  @include mixins.media("tablet-l") {
    width: 180px;
    height: auto;
    bottom: -18%;
  }
}
